<template>
  <div>
    <div class="main-box">
      <div class="tip-text"></div>
      <input type="text" v-model="article.title">
      <div class="tip-text"></div>
      <select v-model="article.type">
        <option value="1">
          
        </option>
        <option value="2">
          
        </option>
        <option value="3">
          
        </option>
        <option value="4">
          
        </option>
        <option value="5">
          
        </option>
      </select>
      <div class="tip-text"></div>
      <input type="text" v-model="article.summary">
      <div class="tip-text"></div>
      <input type="password" v-model="article.password">
      <div class="tip-text"></div>
      <input type="file" id="input-file" @change="imgChange($event)" v-show="false" accept="image/*">
      <label for="input-file">
        <div class="img-box">
          <img :src="article.base64Img">
        </div>
      </label>
      <div class="tip-text"></div>
    </div>
    <mavon-editor @change="change" class="editor"></mavon-editor>
    <div class="btn-box">
      <button :class="['btn',{'btn-hover':isHover}]" @mouseenter="isHover=true"
      @mouseleave="isHover=false" @click="editSubmit"></button>
    </div>
  </div>
</template>
<script>
import { mavonEditor } from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
export default {
  data () {
    return {
      isHover: false,
      article: {
        title: '',
        content: '',
        type: '',
        summary: '',
        base64Img: require('../assets/upload.png'),
        password: ''
      }
    }
  },
  components: {
    mavonEditor
  },
  methods: {
    change (value, render) {
      this.article.content = render
    },
    imgChange (e) {
      const img = e.target.files[0]
      const reader = new FileReader()
      reader.readAsDataURL(img)
      reader.onload = () => {
        this.article.base64Img = reader.result
      }
    },
    editSubmit () {
      this.article.time = Date.now()
      this.$http.post('edit', this.article)
        .then((ret) => {
          if (ret.status !== 200) {
            alert('')
          }
          alert('')
        })
    }
  }
}
</script>
<style scoped>
.main-box {
  box-sizing: border-box;
  background-color: #fff;
  padding: 2vw 3vw;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 60vw;
  height: 58vh;
  margin: 8vh auto;
}
.btn-box {
  width: 60vw;
  background-color: #fff;
  margin: 5vh auto;
  height: 10vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
input,select {
  width: 25vw;
  height: 3.4vh;
  border-radius: 4px;
  border: 1px solid #bfcbd9;
  background-color: #efefef;
}
.tip-text {
  font-size: 1vw;
}
.editor {
  width: 60vw;
  margin: 8vh auto;
}
.btn {
  width: 5vw;
  height: 4vh;
  background-color: #97DFFD;
  color: #fff;
  border: #97DFFD;
  border-radius: 8px;
  font-size: 0.8vw;
}
.btn-hover {
  background-color: #48456D;
  cursor: pointer;
}
#input-file {
  position: absolute;
}
img {
  width: 100%;
  height: auto;
}
.img-box {
  cursor: pointer;
  width:5vw;
  height:5vw;
  border: 1px solid rgb(100, 97, 97);
}
</style>
